/*
 Artmoon - Artificial Moon, created by Soomin K., SpacetimeQ.com, Nov.23, 2018

 STEPS
 (Smaller than a quarter moon)
 1. Draw a half circle with clipPath
 2. Put a glowing effect on the clipped circle (Important: apply the effect after clipping to avoid unnatural edge cut)
 3. Put a black mask according to the phase of the moon: mask by transform:scale in x
 4. Put another half circle black mask to cover the opposite side (since scaleX cannot cover it)
 5. Edges of the shadow area can be beautifully drawn by the stroke on the mask
 6. By applying a circular clipPath to the phased black mask, eliminate outer dark glow.
 (Bigger than a quarter moon)
 1. Put the shadow area first: the full gray circle
 2. Put the bright half circle and then the scaled bright circle according to the phase of the moon
 3. Group the bright shapes together and apply the glow effect

 Phase (Brightness of the moon) change speed depends on the constant angular speed. It's not linear.

*/
import React, {Fragment} from 'react';
import './App.scss';
import clouds from './img/cloud02.png';

const vFix = (v, p=2) => Number.isInteger(v) ? v : parseFloat(v.toFixed(p));

/*
  Draw the phased Moon according to the Moon's age (29.53 days in a cycle)
  - brightness: -1..0..1, 0: New moon, 1: Full moon, negative sign for the fading moon
  - mage, 0: New moon, : Waxing crescent 29.53/2: Full moon, 1.9: Waning crescent
    mage > 29.53/2 means starting to wane from the full moon
    lunations may vary from about 29.18 to 29.93
  - tilt: angle = 0 in Northern Hemisphere, counter-clockwise degree
  - size: length of the one side of the square enclosing the Moon in pixels
  - rM: radius of the Moon
  - cCorona : color of the Lunar Corona
  - cSurface: color of the Moon surface
  - cSpace  : color of the Space background
 */
const Moon = ({
  brightness,
  mage,
  tilt=0,
  size=187,
  rM=50,
  cCorona,
  cSurface="White",
  cSpace="rgba(0,0,50,1)",
  cloud=0}) =>
{
  const bTilt = (tilt !== 0);
  const cDimLight = "rgba(50,50,50,.8)";
  const cDark     = "rgba(10,10,10,1)";
  const cBlack    = "Black";
  let mday;    // moon day: normalized moon age with the cycle of mageC
  let br;      // brightness ratio (approx.) -1..0..1, distance of the light borderline from the center

  const NewMoon = () =>
    <Fragment>
      <CoronaC r={rM} fill={cCorona}  filter="url(#glow05)" opacity=".8" />
      <circle  r={rM} fill={cSurface} filter="url(#glow05)" opacity=".5" />
      <circle  r={rM} fill={cDark} stroke={cDimLight} />
    </Fragment>;

  const FullMoon = () =>
    <Fragment>
      <CoronaC r={rM} fill={cCorona}  filter="url(#glow20)" />
      <circle  r={rM} fill={cSurface} filter="url(#glow20)" />
    </Fragment>;

  const CoronaC = (props) =>
    (cCorona !== undefined) ? <circle {...props} /> : null;

  const PhasedMoon = () => {
    const mageC = 29.53;   // Synodic month, long-term average = 29.530587981
    let rot;     // rotation 0,1:new moon .25:first quarter  .5:full .75:third quarter
    let cosTh;
    let tfMirror = null; // transform mirroring

    if (brightness !== undefined) {  // If the brightness was assigned, calculate other values from this.
      br    = Math.abs(brightness);
      if      (br < 0) br = 0;
      else if (br > 1) br = 1;  // range correction
      cosTh = 1 - 2*br;
      mday  = (Math.acos(cosTh)*mageC)/(2*Math.PI);
      if (brightness < 0)
        mday = mageC - mday;
      rot   = mday/mageC;
    } else {           // calculate from mage
      mday  = mage % mageC;
      rot   = mday/mageC;
      cosTh = Math.cos(rot*Math.PI*2);
      br    = (1 - cosTh)/2
    }

    if (br <= .002) return <NewMoon />;
    if (br >= .995) return <FullMoon />;

    const scaleX = vFix(Math.abs(cosTh));
    // tfMirror will remain null when no transform needed
    if (rot > 2/4)
      tfMirror = "scale(-1,1)";
    if (rot >= 1/4 && rot <= 3/4) {  // Convex: Bigger than quarter of the moon, 
      return (  // the light borderline rotated to the back side...
        <g transform={tfMirror}>
          <CoronaC r={rM} fill={cCorona} filter="url(#glow05)" opacity=".8" />
          <circle  r={rM} fill={cDark} stroke={cDimLight} />
          <g filter="url(#glow10)">
            <CoronaC r={rM} fill={cCorona}  clipPath="url(#clipRight)" />
            <circle  r={rM} fill={cSurface} transform={`scale(${scaleX},1)`} />
            <circle  r={rM} fill={cSurface} clipPath="url(#clipRight)" />
          </g>
        </g>
      );
    } else {  // Concave: Smaller than quarter of the moon,
      let clipLv = "";
      if      (br < .05) clipLv = "1";
      else if (br < .2)  clipLv = "2";
      return (
        <g transform={tfMirror}>
          <CoronaC r={rM} fill={cCorona} filter="url(#glow05)" opacity=".8" />
          <g filter="url(#glow10)">
            <CoronaC r={rM} fill={cCorona}  clipPath={`url(#clipRight${clipLv})`} />
            <circle  r={rM} fill={cSurface} clipPath={`url(#clipRight${clipLv})`} />
          </g>
          <g clipPath={`url(#clipMoon${(br < .2) ? "" : "1"})`}>
            <circle r={rM} fill={cBlack} filter="url(#blur05)" transform={`scale(${scaleX},1.2)`} />
          </g>
          <circle r={rM} fill={cBlack} clipPath="url(#clipLeft)" stroke={cDimLight} />
        </g>
      );
      // {(br > .35) ? <rect x="-20" y={-rM*1.2} width="20" height={rM*1.2*2} fill={cBlack} stroke="none" /> : null}
    }
  }

  const Craters = ({sym="#craters001"}) =>
    <g transform={(rM !== 50) ? `scale(${vFix(rM/50)})` : null}>
      <use xlinkHref={sym} />
      <use xlinkHref={sym} transform="rotate(250) scale(.8)" />
      <use xlinkHref={sym} transform="rotate(50) scale(.4) translate(-25,90)" />
    </g>;

  // Draw North and South poles, according to the compass color
  const Poles = () => {
    if (!bTilt)
      return null;
    return (
      <Fragment>
        <line x1="0" y1={-rM*1.3} x2="0" y2={-rM} stroke="Red"  strokeWidth="2" />
        <line x1="0" y1={rM*1.3}  x2="0" y2={rM}  stroke="Blue" strokeWidth="2" />
      </Fragment>
    );
  }

  const Stars = ({sym="#stars001", random=0}) => {
    const a = (random > 0) ?
      Array.from({length: 12}, () => Math.floor(Math.random()*150)) :
      [ 10, 5,
        20, 10,
         0, 0,
       120, 130,
         0, 0,
       180,
        10];

    return (
      <Fragment>
        <use xlinkHref={sym} x={a[0]}  y={a[1]} />
        <use xlinkHref={sym} x={a[2]}  y={a[3]} transform={`rotate(${a[10]})`} />
        <use xlinkHref={sym} x={a[4]}  y={a[5]} transform={`rotate(${a[10]})`} />
        <use xlinkHref={sym} x={a[6]}  y={a[7]} transform="scale(1,-1)" />
        <use xlinkHref={sym} x={a[8]}  y={a[9]} transform="scale(-.8)" opacity=".5" />
      </Fragment>
    );
  }
    /*
      <use xlinkHref="#stars001" x="160" y="10" transform="scale(.7,.7)" />
      <use xlinkHref="#stars001" x="185" y="13" transform="scale(-.5,-.5)" />
    */

  const Sun = () => {
    if ((brightness === undefined) || (Math.abs(brightness) >= .03))
      return null;
    return (
      <g transform={`translate(60,-30)`} >
        <circle r={rM} filter="url(#glow30)" fill="Orange" />
        <circle r={rM} filter="url(#glow20)" fill="Red"    />
        <circle r={rM} filter="url(#blur05)" fill="Gold"   />
      </g>
    );
  }

  // For safari, both width and height should be specified, otherwise no display at all.
  // Flickering issue in animation when drawn over the Moon:
  // happens only when use the image, other svg objects are fine
  const Cloud = ({sym=clouds}) => {
    if (cloud <= 0)
      return null;
    return (
      <Fragment>
        <image xlinkHref={sym} x="-120" y="-20" width={150} height={80} opacity=".5" />
        <image xlinkHref={sym} x="-10"  y="-60" width={120} height={60} opacity=".7" filter="url(#blur05)" />
      </Fragment>
    );
  }

  const Info = () =>
    <g fontSize="12px" textAnchor="middle">
      <text x="0" y="75" stroke="Olive">{mday.toFixed(2)}</text>
      <text x="0" y="90" stroke="Crimson">{(br*100).toFixed(1) + '%'}</text>
      {bTilt ? <text x="0" y="-70" stroke="DodgerBlue">{tilt.toFixed(1) + '°'}</text> : null}
    </g>;

  return (
    <div>
      <svg viewBox="-100 -100 200 200" width={size} height={size}>
        <rect x="-100" y="-100" width="200" height="200" fill={cSpace} stroke="Green" />
        <Stars />
        <Sun />
        <g transform={bTilt ? `rotate(${-tilt})` : null}>
          <Poles />
          <PhasedMoon />
          <Craters />
        </g>
        <Cloud />
        <Info />
      </svg>
    </div>
  );
}

// Be careful of the fact that symbols are shared all through the document.
// Change in a symbol can affect the whole document.
// Expand filter area to cover the whole view window
// For safari, assigning absolute values resulted in bumpy SourceGraphic. (Report by James C.)
// To avoid this issue, use percentage in x, y, width, and height parameters. Nov.29,2018
// SVG Gaussian Blur in Sarari unexpectedly lightens image: set color-interpolation-filters="sRGB"
// that is default to other browsers
const DefineFilters = ({rM=50}) => {
  const BlurFilter = ({id, stdDev}) =>
    <filter id={id} x="-50%" y="-50%" width="200%" height="200%" colorInterpolationFilters="sRGB">
      <feGaussianBlur in="SourceGraphic" stdDeviation={stdDev} />
    </filter>;

  const GlowFilter = ({id, stdDev}) =>
    <filter id={id} x="-50%" y="-50%" width="200%" height="200%" colorInterpolationFilters="sRGB">
      <feGaussianBlur stdDeviation={stdDev} result="coloredBlur" />
      <feMerge>
        <feMergeNode in="coloredBlur" />
        <feMergeNode in="SourceGraphic" />
      </feMerge>
    </filter>;

  const ClipRectX = ({id, x=0, w=100}) =>
    <clipPath id={id}>
      <rect x={x} y={-100} width={w} height={200} />
    </clipPath>;

  return <svg width="0" height="0">
    <defs>
      <BlurFilter id="blur01" stdDev="1" />
      <BlurFilter id="blur05" stdDev="5" />
      <GlowFilter id="glow05" stdDev="05" />
      <GlowFilter id="glow10" stdDev="10" />
      <GlowFilter id="glow20" stdDev="20" />
      <GlowFilter id="glow30" stdDev="30" />
      <ClipRectX  id="clipLeft"   x={-100} />
      <ClipRectX  id="clipRight1" x={20*rM/50} w={100-(20*rM/50)} />
      <ClipRectX  id="clipRight2" x={10*rM/50} w={100-(10*rM/50)} />
      <ClipRectX  id="clipRight" />
      <clipPath   id="clipMoon"> <circle r={rM} /></clipPath>
      <clipPath   id="clipMoon1"><circle r={rM+1} /></clipPath>
      <symbol id="stars001" overflow="visible" filter="url(#blur01)">
        <circle r=".5"  cx="-80" cy="-80" fill="White" />
        <circle r=".8"  cx="-70" cy="-55" fill="Gold" />
        <circle r="1"   cx="-85" cy="-85" fill="rgba(255,215,0,.6)" />
        <circle r="1.3" cx="-90" cy="-70" fill="rgba(220,220,255,.7)" />
      </symbol>
      <symbol id="craters001" overflow="visible" filter="url(#blur05)" fill="rgba(50,50,50,1)">
        <circle r={20} cx={-20} cy={-12} opacity=".2" />
        <circle r={10} cx={-5}  cy={-25} opacity=".4" />
        <circle r={5}  cx={-10} cy={10}  opacity=".6" />
        <circle r={4}  cx={10}  cy={-20} opacity=".7" />
        <circle r={6}  cx={30}  cy={-12} opacity=".5" />
      </symbol>
    </defs>
  </svg>;
}

// Animation by requestAnimationFrame
// Check the performance and then adjust the rendering interval (frame rate) each time
// - When the window is in background mode the elapsed time can be indefinitely long.
class LiveMoon extends React.Component {
  constructor(props) {
    super(props);
    this.lastTS    = 0;
    this.interval  = 1000/5;   // 1000 ms / fps
    this.idleCount = 0;
    this.runCount  = 0;
    this.bTiltAnimation = (this.props.tiltAngle !== undefined);  // typeof ... != "undefined"
    this.state = {
      mage: this.props.mageStart ? this.props.mageStart : 0,
      tilt: this.props.tiltAngle ? this.props.tiltAngle : 0
    };  // moon age
  }

  componentDidMount() {  // start loop, be sure to bind the loop function
    if (!this._frameId)
      this._frameId = window.requestAnimationFrame(this.loop.bind(this));
  }

  componentWillUnmount() {  // stop loop
    window.cancelAnimationFrame(this._frameId);
  }

  loop(timestamp) {
    let elapsed = timestamp - this.lastTS;
    if (elapsed < this.interval) {  // Skip the loop for this time period
      this.idleCount++;
      this._frameId = window.requestAnimationFrame(this.loop.bind(this));
      return;
    }
    /*
    if (elapsed > 1000) { // for debugging
      console.log("Elapsed:", elapsed, "Idle Count:", this.idleCount);
    }
    */
    this.interval = (this.idleCount <= 10) ?  // Busy?
      Math.round(this.interval*1.1) :
      Math.min(Math.round(this.interval*.9),  200);
    this.runCount++;
    //if (this.runCount  < 10 || this.runCount % 10 === 0)
    //  console.log("Run:", this.runCount, "Idle Count:", this.idleCount, "Interval:", this.interval);
    this.lastTS    = timestamp;
    this.idleCount = 0;
    let tilt = 0;
    if (this.bTiltAnimation) {
      tilt = this.state.tilt + elapsed*.5/200;
      if (tilt > 360)  // wrap
        tilt = 0;
    }
    this.setState(state => ({
      mage: this.state.mage + elapsed*.15/200,
      tilt: tilt
    }));
    this._frameId = window.requestAnimationFrame(this.loop.bind(this));
  }

  render() {
    return <Moon mage={this.state.mage} tilt={this.state.tilt} {...this.props} />;
  }
}

const MoonShots = (props) => {
  let lnsMoon = [];

  for (let b=1; b > -1; b -= props.db)
    lnsMoon.push(<Moon key={b} brightness={b} cloud={Math.round(Math.random()*.6)} {...props} />);

  return <Fragment>{lnsMoon}</Fragment>
}

// Screen Size
// iPhone 5/SE:  320x568
// iPhone 6/7/8: 375x667
// iPhone 6/7/8: 414x736
// iPhone X:     375x812

class App extends React.Component {
  constructor(props) {
    super(props);
    console.log("Window size: ", window.innerWidth, window.innerHeight);
    this.msize = 150;
    if (window.innerWidth < 450)
      this.msize = Math.trunc(window.innerWidth/2);  // make two moon blocks fit into the width for mobile
  }

  componentDidMount() {
    console.log("DOM count: ", document.getElementsByTagName('*').length);
    console.log("Navigator userAgent:", navigator.userAgent, "Vendor:", navigator.vendor);
  }

  render() {
    const mageC = 29.53;
    const ms = this.msize;
    return (
      <div className="App">
        <h3 className="title">Artificial Moon (人工の月) Experiment- SVG</h3>
        <div className="content">
          <DefineFilters />
          <LiveMoon size={ms} />
          <LiveMoon size={ms} tiltAngle={360-60} />
          <LiveMoon size={ms} mageStart={14} tiltAngle={0} cSurface="Gold" cCorona="Orange" />
          <LiveMoon size={ms} mageStart={7}  cSurface="Blue" cCorona="Blue" />
          <LiveMoon size={ms} mageStart={21} cSurface="RosyBrown" cCorona="Red" />
          <Moon size={ms} mage={mageC/2} cCorona={'rgba(255,215,0,.8)'} />
          <Moon size={ms} mage={mageC/2} cCorona={'rgba(255,0,0,.8)'} />
          <Moon size={ms} mage={mageC/2} cCorona={'rgba(0,0,255,.9)'} />
          <Moon size={ms} mage={0}       cCorona="Blue" />
          <Moon size={ms} mage={.5} />
          <Moon size={ms} mage={mageC*1/8} cloud={1} />
          <Moon size={ms} mage={mageC*2/8} cloud={1} />
          <Moon size={ms} mage={mageC*3/8} cloud={1} />
          <Moon size={ms} mage={mageC*4/8} cloud={1} cCorona="Blue" cSpace="White" />
          <Moon size={ms} mage={mageC*5/8} cloud={1} cSpace="Wheat" />
          <Moon size={ms} mage={mageC*6/8} cloud={1} cSpace="DarkBlue" />
          <Moon size={ms} mage={mageC*7/8}           cSpace="MediumBlue" />
          <Moon size={ms} mage={mageC-.5}            cSpace="Blue" />
          <Moon size={ms} mage={4} cCorona="Yellow" cloud={1} cSpace="MidnightBlue" />
          <MoonShots size={ms} db={.4} cCorona="Crimson" cSpace="Black" />
          <LiveMoon size={ms*4} mageStart={0} tiltAngle={10} cSurface="LightBlue" cCorona="Blue" />
        </div>
      </div>
    );
  }
}

export default App;

